* {
  box-sizing: inherit;
  margin: 0;
  padding: 0;
}

html {
  box-sizing: border-box;
  font-size: 62.5%;
}

body {
  color: #333;
  height: 100vh;
  background: linear-gradient(to top left, #28b487, #7dd56f);
  justify-content: center;
  align-items: flex-start;
  font-family: sans-serif;
  line-height: 1.5;
  display: flex;
  position: relative;
}

.show-modal {
  color: #444;
  cursor: pointer;
  background-color: #fff;
  border: none;
  border-radius: 10rem;
  margin: 5rem 2rem;
  padding: 1.75rem 3.5rem;
  font-size: 2rem;
  font-weight: 600;
}

.close-modal {
  color: #333;
  cursor: pointer;
  background: none;
  border: none;
  font-size: 5rem;
  position: absolute;
  top: 1.2rem;
  right: 2rem;
}

h1 {
  margin-bottom: 2rem;
  font-size: 2.5rem;
}

p {
  font-size: 1.8rem;
}

.hidden {
  display: none;
}

.modal {
  width: 70%;
  z-index: 10;
  background-color: #fff;
  border-radius: 5px;
  padding: 6rem;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  box-shadow: 0 3rem 5rem #0000004d;
}

.overlay {
  width: 100%;
  height: 100%;
  -webkit-backdrop-filter: blur(3px);
  backdrop-filter: blur(3px);
  z-index: 5;
  background-color: #0009;
  position: absolute;
  top: 0;
  left: 0;
}

